

























import { Vue, Component, Prop } from "vue-property-decorator";
const vueLogo = require("../../assets/images/logo-vue.png");
import { Draggable } from "draggable-vue-directive";
@Component({
    directives: {
        Draggable
    }
})
export default class App extends Vue { 
    vueLogo = vueLogo;
    boxes: number = 0;

    addBox() {
        this.boxes++;
    }

    clearAll() {
        this.boxes = 0;
    }
}



